import {NgModule, ModuleWithProviders} from '@angular/core';
import {CommonModule} from '@angular/common';
import {VdlPopover, VdlPopoverWindow} from './popover';
import {VdlPopoverPredefined} from './popover-predefined';
import {VdlPopoverConfig} from './popover-config';
import {VdlIconModule} from '../icon/icon';

export * from './popover';
export * from './popover-predefined';

@NgModule({
  declarations: [VdlPopover, VdlPopoverWindow, VdlPopoverPredefined],
  imports: [CommonModule, VdlIconModule],
  exports: [VdlPopover, VdlPopoverPredefined],
  entryComponents: [VdlPopoverWindow]
})
export class VdlPopoverModule {
  static forRoot(): ModuleWithProviders {
    return {ngModule: VdlPopoverModule, providers: [VdlPopoverConfig]};
  }
}

